<script lang="ts">import { FieldConfig } from "@ProductConfig/tabs/Fields/Field/field-config";
import { getSecondaryFieldExpression } from "@ProductConfig/utils/formula-helper";
import { longpress } from "@ProductConfig/utils/longpress";
import { dpa, dpa_ui } from "@ProductConfig/variables";
import { dp_trans } from "@utils/trans-helper";
import { values } from "@utils/utils";
import { slide } from "svelte/transition";
export let field;
export let display_options;
export let insert;
export let insertField;
export let insertSecondaryField;
let fieldConfig = FieldConfig.get(field.type || 1);
const secondary_value_title = dp_trans("Long press to use the secondary value");
let title = fieldConfig.secondary ? secondary_value_title + " " + getSecondaryFieldExpression(field) : "";
function insertSecondary() {
    if (fieldConfig.secondary) {
        insertSecondaryField(field);
    }
}
let display_options_list = false;
function toggleOptionsList() {
    if ($dpa_ui.displayed_options === field.id) {
        $dpa_ui.displayed_options = 0;
    }
    else {
        $dpa_ui.displayed_options = field.id;
    }
}
let longpressed = false;
function insertOptionValue(value) {
    if (longpressed) {
        longpressed = false;
        return;
    }
    insert(value.toString());
    $dpa_ui.displayed_options = 0;
    longpressed = false;
}
function insertOptionSecondaryValue(value) {
    longpressed = true;
    insert(value.toString());
    $dpa_ui.displayed_options = 0;
    longpressed = false;
}
function hideOptionsList() {
    if ($dpa_ui.displayed_options === +field.id) {
        $dpa_ui.displayed_options = 0;
    }
}
</script>

<svelte:window on:click={hideOptionsList}/>

<button
  use:longpress on:longpress={insertSecondary}
  {title}
  class="dp_equation_btn"
  on:click={() => insertField(field)}
>
  {field.name}
  {#if display_options && fieldConfig.list && values(field.options).length > 0}
    <button class="dp_btn" on:click|stopPropagation={toggleOptionsList} style="color: #fff;"
            title={dp_trans('Click to insert an option')}>
      <i class="material-icons">keyboard_arrow_down</i>
    </button>
    {#if $dpa_ui.displayed_options === +field.id}
      <div class="dp_options_list" transition:slide={{duration: 200}} on:click|stopPropagation>
        <ul>
          {#each values(field.options) as option (option.id)}
            {#if option.label}
              <li
                title="{`${dp_trans('Value')}: ${option.value}, ${dp_trans('Secondary value')}: ${option.secondary_value} (${secondary_value_title})`}"
                on:click|stopPropagation={() => insertOptionValue(option.value)}
                use:longpress on:longpress={() => insertOptionSecondaryValue(option.secondary_value)}
              >
                <button class="dp_btn" style="color: #fff;">
                  {option.label[dpa.id_default_lang] || `(${dp_trans("empty")})`}
                </button>
              </li>
            {/if}
          {/each}
        </ul>
      </div>
    {/if}
  {/if}
</button>

<style lang="scss">.dp_equation_btn > button {
  border-radius: 3px;
  padding: 0 5px;
}

.dp_equation_btn:hover > button {
  box-shadow: 0 0 0 1px #fff;
}

.dp_options_list {
  position: absolute;
  top: calc(100% + 1px);
  left: 0;
  min-width: 150px;
  max-width: 500px;
  white-space: nowrap;
  max-height: 600px;
  overflow: auto;
  border-radius: 3px;
  text-align: left;
  background-color: #00AFF0;
  z-index: 1;
}
.dp_options_list ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}
.dp_options_list ul li {
  padding: 5px 10px;
}
.dp_options_list ul li:hover {
  background-color: #0bbdff;
}
.dp_options_list ul li button {
  text-align: left;
  width: 100%;
  height: 100%;
}</style>
