<style>
  .grid-container {
    position: relative;
    border: 1px solid #eee;
    padding: 5px;
    margin-bottom: 15px;
  }

  .grid-delete {
    display: none;
    position: absolute;
    top: 5px;
    right: 5px;
  }

  .grid-container:hover .grid-delete {
    display: block;
  }

  .fields {
    display: inline-flex;
    justify-content: flex-start;
  }

  .fields > div {
    margin-right: 10px;
  }

  select {
    width: 250px;
  }

  .grid {
    margin-top: 10px;
    display: grid;
  }

  .info {
    padding: 5px;
    background-color: #eee;
  }

  .info .column-header {
    text-align: right;
  }

  .blue {
    color: #2196f3;
  }

  .green {
    color: #4caf50;
  }

