<div class="grid-container" transition:fade>
  <button
    class="grid-delete btn btn-danger"
    on:click|preventDefault={deleteGrid}
    title={dp_trans('Delete grid')}
    data-cy="delete_grid"
  >
    <i class="material-icons">delete</i>
  </button>

  <div class="fields">
    <div>
      <label>{dp_trans('Target field')}:</label>
      <select class="form-control" bind:value="{grid.id_field_target}" on:change={saveGrid} data-cy="target_field">
        <option value="0">--</option>
        {#each reorder($dpa_fields) as field}
          <option value={field.id}>{field.name}</option>
        {/each}
      </select>
    </div>
    <div>
      <label>{dp_trans('Column field')}</label>
      <select class="form-control blue" bind:value="{grid.id_field_column}" on:change={saveGrid} data-cy="column_field">
        <option value="0">--</option>
        {#each reorder($dpa_fields) as field}
          <option value={field.id}>{field.name}</option>
        {/each}
      </select>
    </div>
    <div>
      <label>{dp_trans('Row field')}</label>
      <select class="form-control green" bind:value="{grid.id_field_row}" on:change={saveGrid} data-cy="row_field">
        <option value="0">--</option>
        {#each reorder($dpa_fields) as field}
          <option value={field.id}>{field.name}</option>
        {/each}
      </select>
    </div>
  </div>

  <div class="grid" on:keyup={keyUp} on:mouseleave={clearHover} style={grid_style}>
    <div class="info">
      <div class="column-header blue">
        {$dpa_fields[grid.id_field_column] ? $dpa_fields[grid.id_field_column].name : '--'}
        <i class="material-icons">keyboard_arrow_right</i>
      </div>
      <div class="row-header green">
        {$dpa_fields[grid.id_field_row] ? $dpa_fields[grid.id_field_row].name : '--'}
        <i class="material-icons">keyboard_arrow_down</i>
      </div>
    </div>
    <ColumnHeader {grid}/>
    <Rows {grid} {deleteColumn}/>
  </div>
</div>
