<script lang="ts">import { dpa } from "@ProductConfig/variables";
import { dp_trans } from "@utils/trans-helper";
export let product_step;
export let condition;
export let toggleStep;
$: visible = !condition.hidden_steps.includes(product_step.id);
</script>

<button on:click|preventDefault={(ev) => toggleStep(ev, product_step.id)}
        title="{dp_trans('Click to toggle the step visibility')}"
        class="dp_equation_btn dp_step_btn"
        class:dp_condition_hidden={!visible}>
  {product_step.step.label[dpa.id_default_lang]}
</button>
