import type { IProductFieldGroup } from "@ProductConfig/types/GroupsStore";
import { GroupsStoreData } from "@ProductConfig/types/GroupsStore";
import { writable } from "svelte/store";

export function createGroupsStore (grids) {
  const { subscribe, set, update } = writable<GroupsStoreData>(grids);

  return ({
    set,
    update,
    subscribe,

    updateProductFieldGroup: (product_group: IProductFieldGroup) => {
      return update((state) => {
        state.product_field_groups[product_group.id] = product_group;
        return state;
      });
    },

    updateData: (data) => {
      return update((state) => {
        return { ...state, ...data };
      });
    }
  });
}
