<script lang="ts">import { dpa_fields, dpa_filter } from "@ProductConfig/variables";
import { dp_trans } from "@utils/trans-helper";
import { values } from "@utils/utils";
export let placeholder = dp_trans("Filter fields");
export let filter_store = dpa_filter;
$: {
    if (values($dpa_fields).length <= 7 && $filter_store) {
        $filter_store = null;
    }
}
</script>

{#if values($dpa_fields).length > 7}
  <div class="filter-container">
    {#if $filter_store}
      <button class="dp_btn dp-clear-btn" data-cy="clear-filter" on:click={() => filter_store.set('')}>
        <i class="material-icons">clear</i>
      </button>
    {/if}
    <input class="form-control"
           data-cy="fields-filter"
           bind:value={$filter_store}
           {placeholder}
           title="{dp_trans('You can use multiple filters separated by spaces')} (width height len qu)"
    >
  </div>
{/if}

<style>
  .filter-container {
    position: relative;
    padding: 5px;
    border-bottom: 1px solid #EEE;
  }

  input {
    padding-left: 25px;
  }

  .dp-clear-btn {
    position: absolute;
    top: 10px;
    left: 5px;
  }

</style>
