import { dpa, dpa_message, dpa_post, dpa_ui } from "@ProductConfig/variables";

export async function copyFromProduct (id_source_product, link, clear) {
  if (!id_source_product) {
    alert(dpa_message.no_product);
    return false;
  }

  if (!confirm(dpa_message.warn_config)) {
    return false;
  }

  const data = {
    action           : "copy_product_config",
    id_target_product: dpa.id_product,
    id_source_product,
    link             : link ? 1 : 0,
    clear            : clear ? 1 : 0,
  };

  dpa_ui.setLoading(true);

  const res = await dpa_post.post(dpa.controllers.product_settings, data);

  dpa_ui.setLoading(false);

  if (res.success) {
    location.reload();
  }
}
