import type { IAdminMessages } from "@interfaces/admin-messages"
import type { IPostHelper } from "@interfaces/post-helper"
import { createCombinationsStore } from "@ProductConfig/stores/combinations-store"
import { createConditionsStore } from "@ProductConfig/stores/conditions-store"
import { createExecStore } from "@ProductConfig/stores/exec-store"
import { createFieldFormulasStore } from "@ProductConfig/stores/field-formulas-store"
import { createFieldsStore } from "@ProductConfig/stores/fields-store"
import { createGridsStore } from "@ProductConfig/stores/grids-store"
import { createGroupsStore } from "@ProductConfig/stores/groups-store"
import { createIntervalsStore } from "@ProductConfig/stores/intervals-store"
import { createProportionsStore } from "@ProductConfig/stores/proportions-store"
import { createStepsStore } from "@ProductConfig/stores/steps-store"
import { createUiStore } from "@ProductConfig/stores/ui-store"
import type { CombinationsStore } from "@ProductConfig/types/CombinationsStore"
import type { ConditionsStore } from "@ProductConfig/types/ConditionsStore"
import type { IDynamicProductAdmin } from "@ProductConfig/types/DynamicProduct"
import type { ExecStore } from "@ProductConfig/types/ExecStore"
import type { FieldFormulasStore } from "@ProductConfig/types/FieldFormulasStore"
import type { AdminFieldStore } from "@ProductConfig/types/FieldsStore"
import type { GridsStore } from "@ProductConfig/types/GridsStore"
import type { GroupsStore } from "@ProductConfig/types/GroupsStore"
import type { IntervalsStore } from "@ProductConfig/types/IntervalsStore"
import type { ProportionsStore } from "@ProductConfig/types/ProportionsStore"
import { StepsStore } from "@ProductConfig/types/StepsStore"
import type { AdminUiStore } from "@ProductConfig/types/UiStore"
import { reorderLanguages } from "@ProductConfig/utils/language"
import { PostHelper } from "@ProductConfig/utils/post-helper"
import { filterFields } from "@ProductConfig/utils/stores/filtered-fields"
import { reorder } from "@utils/reorder"
import { values } from "@utils/utils"
import { derived, Writable, writable } from "svelte/store"

export const dpa: IDynamicProductAdmin = window.dpa
dpa.languages = reorderLanguages(dpa.languages, dpa.id_default_lang)

export const dpa_ui: AdminUiStore = createUiStore({
  loading          : false,
  lang_input_open  : false,
  open_modal       : null,
  duplicate_fields : [],
  tab_errors       : new Map([]),
  displayed_options: 0,
  nb_linked_configs: dpa.nb_linked_configs,
  id_lang_active   : dpa.id_default_lang.toString(),
})

export const dpa_filter: Writable<string> = writable("")
export const dpa_modal_filter: Writable<string> = writable("")

export const dpa_fields: AdminFieldStore = createFieldsStore(dpa.fields)

export const dpa_filtered_fields: Partial<AdminFieldStore> = derived([dpa_fields, dpa_filter], filterFields)
export const dpa_modal_filtered_fields: Partial<AdminFieldStore> = derived([dpa_fields, dpa_modal_filter], filterFields)

export const dpa_message: IAdminMessages = window.dpa_message

export const dpa_combinations: CombinationsStore = createCombinationsStore({
  combination_fields: dpa.combination_fields,
  combination_values: dpa.combination_values,
  visibility        : dpa.visibility_values,
})


export const dpa_conditions: ConditionsStore = createConditionsStore(dpa.conditions)

export const dpa_field_formulas: FieldFormulasStore = createFieldFormulasStore(dpa.field_formulas)

export const dpa_proportions: ProportionsStore = createProportionsStore(dpa.proportions)

export const dpa_intervals: IntervalsStore = createIntervalsStore(dpa.intervals)

export const dpa_grids: GridsStore = createGridsStore(dpa.grids)

export const dpa_groups: GroupsStore = createGroupsStore({
  product_field_groups: dpa.product_field_groups,
  field_groups        : dpa.field_groups,
})

export const dpa_steps: StepsStore = createStepsStore({
  product_steps: reorder(dpa.product_steps),
  steps        : values(dpa.steps),
})

export const dpa_exec: ExecStore = createExecStore(dpa.exec_order)

export const dpa_post: IPostHelper = new PostHelper()

export const showSuccessMessage: Function = window.showSuccessMessage
export const showErrorMessage: Function = window.showErrorMessage

declare global {
  interface Window {
    dpa: IDynamicProductAdmin;
    dp_id_module: number;
    dpa_message: IAdminMessages;
    showSuccessMessage: Function;
    showErrorMessage: Function;
    tinyMCE: any;
    tinySetup: any;
    dp_module_link: string;
    dp_translations: { [key: string]: string };
    Pickr: any;
  }
}
