<script lang="ts">export let saveConfig;
import Switch from "@ProductConfig/components/Switch.svelte";
import { dpa } from "@ProductConfig/variables";
import { dp_trans } from "@utils/trans-helper";
function saveInput(name, input) {
    saveConfig({ detail: { name, value: input.value } });
}
</script>

<div class="dp_config_container">
  <div class="form-group">
    <div>
      <div style="display: inline-block;">
        <div><label class="control-label">{dp_trans('Displayed price')}</label></div>
        <div class="input-group money-type">
          <span class="input-group-addon input-group-text">{dpa.currency.sign}</span>
          <input
            type="text"
            data-name="displayed_price"
            class="dp_ajax_input form-control"
            value={dpa.config.displayed_price}
            on:change={(e) => saveInput('displayed_price', e.target)}
            style="max-width: 130px;"/>
        </div>
      </div>

      <div style="display: inline-block;">
        <div><label class="control-label">{dp_trans('Price unit')}</label></div>
        <div class="input-group">
          <input
            type="text"
            data-name="displayed_price_label"
            class="dp_ajax_input form-control"
            placeholder={dp_trans('Per kilo, per litre')}
            value={dpa.config.displayed_price_label}
            on:change={(e) => saveInput('displayed_price_label', e.target)}
            style="max-width: 200px"/>
        </div>
      </div>
      <div class="help-block">{dp_trans('You can configure a displayed price if your product has a price of 0')}</div>
    </div>
  </div>

  <Switch
    name="display_starting_from"
    value={dpa.config.display_starting_from}
