<script lang="ts">import { fieldsSelection, FieldsSelectionTopics } from "@ProductConfig/stores/fields-selection";
import { dp_trans } from "@utils/trans-helper";
export let field;
export let saveField;
function toggleFavorite() {
    field.favorite = +!field.favorite;
    saveField();
}
function toggleCommon() {
    field.common = +!field.common;
    saveField();
}
let checkbox;
function handleFieldSelected(ev) {
    fieldsSelection.setSelected(field.id, ev.target.checked);
}
fieldsSelection.on(FieldsSelectionTopics.SELECT_ALL, () => {
    if (checkbox) {
        checkbox.checked = true;
        fieldsSelection.setSelected(field.id, true);
    }
});
fieldsSelection.on(FieldsSelectionTopics.DESELECT_ALL, () => {
    if (checkbox) {
        checkbox.checked = false;
        fieldsSelection.setSelected(field.id, false);
    }
});
</script>

{#if !$fieldsSelection.selecting}
  {#if !+field.linked}
    {#if !field.common}
      <button class="dp_btn dp_fav_button"
              on:click={toggleFavorite}
              class:favorite={field.favorite}
              title={field.favorite ? dp_trans('Remove field from favorites') : dp_trans('Add field to favorites')}
      >
        <i class="material-icons">star</i>
      </button>
    {/if}

    {#if !field.favorite}
      <button class="dp_btn dp_common_btn"
              on:click={toggleCommon}
              class:common={field.common}
              title={field.common ? dp_trans('Remove field from common fields') : dp_trans('Set as common field')}
      >
        <i class="material-icons">share</i>
      </button>
    {/if}
  {/if}
{:else}
  <input type="checkbox"
         bind:this={checkbox}
         data-name="field-checkbox"
         on:change={handleFieldSelected}
         data-cy="field-checkbox"
  />
{/if}

<style>
  .favorite, .common {
    color: #6e6e6e;
  }

</style>
