import { dpa } from "@ProductConfig/variables";
// @ts-ignore
import ProductConfig from "./ProductConfig.svelte";

let app;

document.addEventListener("DOMContentLoaded", () => {
  if (typeof dpa.id_product === "number") {
    const target = document.getElementById("dp_product_config");
    if (target) {
      app = new ProductConfig({
        target,
      });
    }
  }
});
