import type { IField, IFields } from "@interfaces/field-interface"
import { dpa } from "@ProductConfig/variables"
import { reorder } from "@utils/reorder"
import { values } from "@utils/utils"

export function filterFields ([fields, criteria], set) {
  let ordered_fields = reorder(fields as IFields)
  if (!criteria) {
    set(ordered_fields)
    return
  }
  criteria = criteria.replace(/_/g, "")
  let filters: string[] = criteria.split(" ")

  function matchCritera (field: IField, filter: string) {
    return (field.name || "").replace(/_/g, "").toLowerCase().includes(filter) ||
      values(field.label as string[]).find(label => (label || "").toLowerCase().includes(filter)) ||
      (dpa.field_types[field.type]?.label ?? "").toLowerCase().includes(filter)
  }

  const filtered_fields = ordered_fields.filter(field => {
    return filters.map(filter => matchCritera(field, filter)).includes(true)
  })
  set(filtered_fields)
}
