import { writable } from "svelte/store";

export function createCombinationsStore<CombinationsStore> (state) {
  const { set, update, subscribe } = writable(state);

  return {
    subscribe,
    update,
    set,
    updateData: (data) => {
      return update((state) => {
        return { ...state, ...data };
      });
    }
  };
}