<script lang="ts">import { dpa_groups } from "@ProductConfig/variables";
import { dp_trans } from "@utils/trans-helper";
export let product_field_group;
export let condition;
export let toggleGroup;
let group = $dpa_groups.field_groups[product_field_group.id_field_group];
let visible;
$: visible = !condition.hidden_groups.includes(product_field_group.id);
</script>

<button on:click|preventDefault={(ev) => toggleGroup(ev, product_field_group.id)}
        title="{dp_trans('Click to toggle the group visibility')}"
        class="dp_equation_btn dp_field_group_btn"
        class:dp_condition_hidden={!visible}
>
  {group.label}
</button>
