<script lang="ts">import { copyFromProduct } from "@ProductConfig/utils/config";
import { dpa } from "@ProductConfig/variables";
import { select2 } from "@utils/select2";
import { dp_trans } from "@utils/trans-helper";
import { values } from "@utils/utils";
let source_product;
let link = false;
let clearConfig = false;
function copy() {
    let id_source_product = +source_product.value;
    copyFromProduct(id_source_product, link, clearConfig);
}
</script>

{#if dpa.products && values(dpa.products).length && !(dpa.config.active && dpa.products.length === 1)}
  <div class="form-group">
    <label class="control-label">{dp_trans('Load configuration')}</label>
    <div data-cy="load-configuration">
      <select class="dp_chosen form-control" style="width: 250px;" use:select2 bind:this={source_product}>
        <option value="0">{dp_trans('Select a product')}</option>
        {#each values(dpa.products) as prod}
          {#if prod.id_product !== dpa.id_product}
            <option value={prod.id_product}>{prod.label}</option>
          {/if}
        {/each}
      </select>
      <button class="btn btn-primary" data-cy="load-configuration-btn" on:click|stopPropagation={copy}>
        <i class="material-icons">content_copy</i>
        {dp_trans('Load')}
      </button>
      <div>
        <label for="link_config"
               title={dp_trans('Will use the same configuration instead of copying it')}
               style="cursor: pointer;"
        >
          <input type="checkbox" id="link_config" style="vertical-align: middle;" bind:checked={link}> <span
          style="vertical-align: middle;"
        >{dp_trans("Link instead of copying")}</span>
        </label>
      </div>
      {#if link}
        <div>
          <label for="clear_config"
                 title={dp_trans('Will not preserve the current configuration')}
                 style="cursor: pointer;"
          >
            <input type="checkbox" id="clear_config" style="vertical-align: middle;" bind:checked={clearConfig}> <span
            style="vertical-align: middle;"
          >{dp_trans("Clear the current configuration")}</span>
          </label>
        </div>
      {/if}
      <div class="help-block">
        {dp_trans('You can copy the selected product configuration to the current product')}
      </div>
    </div>
  </div>
{/if}
