import { StepsStoreData } from "@ProductConfig/types/StepsStore";
import { reorder } from "@utils/reorder";
import { values } from "@utils/utils";
import { writable } from "svelte/store";

export function createStepsStore (grids) {
  const { subscribe, set, update } = writable(grids);

  return ({
    set,
    update,
    subscribe,
    setStep   : (id_field, id_step) => {
      return update((state) => {
        state.fields[id_field].id_step = id_step;
        return state;
      });
    },
    updateData: (data: StepsStoreData) => {
      return update(() => ({
        product_steps: reorder(data.product_steps),
        steps        : values(data.steps),
      }));
    }
  });
}
