import { dpa, dpa_combinations, dpa_post } from "@ProductConfig/variables";

export async function saveVisibility (visible, id_attribute, id_field = 0) {
  const res: any = await dpa_post.post(dpa.controllers.product_visibility, {
    action : "save_state",
    id_product: dpa.id_product,
    visible: visible,
    id_attribute,
    id_field,
  });
  if (res.success) {
    dpa_combinations.updateData(res.combinations);
  }
}
