import { trigger } from "@utils/utils";

export function sortable (node) {
  const jqnode = jQuery(node);
  jqnode.sortable({
    handle              : ".dp_sort",
    revert              : 100,
    helper              : "clone",
    axis                : "y",
    forcePlaceholderSize: true,
    placeholder         : "dp_field_formula_container ui-sortable-placeholder",
    stop                : () => {
      trigger(node, "sorted", { order: jqnode.sortable("toArray") });
    },
  });
}
