import type { IPostHelper } from "@interfaces/post-helper";
import { dpa, dpa_message } from "@ProductConfig/variables";
import jqXHR = JQuery.jqXHR;

declare const showSuccessMessage: Function;
declare const showErrorMessage: Function;

export class PostHelper implements IPostHelper {

  private requests = new Map<string, jqXHR[]>();
  private timeouts = new Map<string, number>();

  public throttle (context: string, delay: number): Promise<void> {
    return new Promise<void>(resolve => {
      let existing = this.timeouts.get(context);
      if (existing) {
        clearTimeout(existing);
      }
      // @ts-ignore
      let id: number = setTimeout(() => {
        resolve();
      }, delay);
      this.timeouts.set(context, id);
    });
  }

  public post (url: string, data: any): any {
    data.ajax = true;

    if (!data.id_product) {
      data.id_product = dpa.id_source_product;
    }

    let request_name = data.action;
    if (data.debounced) {
      let requests = this.requests.get(request_name) ?? [];
      requests.forEach(req => req.abort());
      this.requests.set(request_name, []);
    }

    let request = jQuery.post({
      url,
      data,
      dataType: "json",
      success : function (response) {
        if (!data.no_msg) {
          if (response.success) {
            if (typeof showSuccessMessage === "function") {
              showSuccessMessage(dpa_message.success);
            }
          } else if (!data.no_error_msg) {
            if (typeof showErrorMessage === "function") {
              showErrorMessage(response.message);
            }
          }
        }
      }
    });

    if (request) {
      this.requests.set(request_name, [request]);
    }

    return request;
  }
}
