<script lang="ts">export let file_input;
import { dpa, dpa_grids, dpa_message, showErrorMessage, showSuccessMessage } from "@ProductConfig/variables";
import "@views/ts/plugins/jquery.ajaxfileupload.js";
function upload(node) {
    jQuery(node).ajaxfileupload({
        action: dpa.controllers.product_grids,
        valid_extensions: ["csv"],
        params: {
            action: `import_csv`,
            id_product: dpa.id_source_product,
            ajax: true
        },
        onStart: () => {
        },
        onComplete: (response) => {
            if (response.success) {
                dpa_grids.updateGrid(response.grid);
                showSuccessMessage(dpa_message.success);
            }
            else {
                showErrorMessage(response.message);
            }
            node.value = "";
        }
    });
}
</script>

<div>
  <input type="file" name="csv" use:upload bind:this={file_input}/>
</div>

<style>
  input[type=file] {
    position: fixed;
    top: -10000px;
    left: -10000px;
  }

</style>
