<script lang="ts">import TabLink from "@ProductConfig/components/TabLink.svelte";
import Steps from "@ProductConfig/tabs/Steps.svelte";
import { dpa } from "@ProductConfig/variables";
import { values } from "@utils/utils";
import { onMount } from "svelte";
import LinkedConfigHeader from "./components/LinkedConfigHeader.svelte";
import Links from "./components/Links.svelte";
import Loader from "./components/Loader.svelte";
import Combinations from "./tabs/Combinations.svelte";
import Conditions from "./tabs/Conditions.svelte";
import FieldFormulas from "./tabs/FieldFormulas.svelte";
import Fields from "./tabs/Fields.svelte";
import Formulas from "./tabs/Formulas.svelte";
import Grids from "./tabs/Grids.svelte";
import Groups from "./tabs/Groups.svelte";
import Intervals from "./tabs/Intervals.svelte";
import Proportions from "./tabs/Proportions.svelte";
import Settings from "./tabs/Settings.svelte";
import TabsHeader from "./tabs/TabsHeader.svelte";
import Visibility from "./tabs/Visibility.svelte";
import "./utils/table-navigator.ts";
import { tabs } from "./utils/tabs";
onMount(() => {
    document.getElementById("module_dynamicproduct").classList.add("dp_style");
});
function detectEnter(e) {
    if (e.key === "Enter" && e.target.tagName.toLowerCase() === "input") {
        var evt = document.createEvent("HTMLEvents");
        evt.initEvent("change", false, true);
        e.target.dispatchEvent(evt);
    }
}
function preventClickDefault(e) {
    if (e.target.tagName.toLowerCase() === "button") {
        e.preventDefault();
    }
}
</script>

<svelte:body on:keyup={detectEnter}/>

<TabLink/>

<Links/>

<div id="dp_product_settings" style="margin-bottom:150px;" on:click={preventClickDefault}>
  <Loader/>

  <LinkedConfigHeader/>

  <div use:tabs>
    <TabsHeader/>

    <Settings/>
    <Fields/>
    <Formulas/>
    {#if dpa.combinations && values(dpa.combinations).length}
      <Combinations/>
      <Visibility/>
    {/if}
    <Conditions/>
    <FieldFormulas/>
    <Proportions/>
    <Intervals/>
    <Grids/>
    <Groups/>
    <Steps/>
  </div>
</div>
