<script lang="ts">import { fieldsSelection, FieldsSelectionTopics } from "@ProductConfig/stores/fields-selection";
import { dp_trans } from "@utils/trans-helper";
import { onMount } from "svelte";
let checkbox;
function handleChange() {
    if (!$fieldsSelection.selecting) {
        checkbox.checked = false;
        checkbox.indeterminate = true;
        fieldsSelection.setSelecting(true);
    }
    else {
        checkbox.indeterminate = false;
        checkbox.checked ?
            fieldsSelection.dispatch(FieldsSelectionTopics.SELECT_ALL) :
            fieldsSelection.dispatch(FieldsSelectionTopics.DESELECT_ALL);
    }
}
onMount(() => fieldsSelection.subscribe(state => {
    if (!state.selecting) {
        checkbox.checked = false;
        checkbox.indeterminate = false;
    }
}));
</script>

<input bind:this={checkbox}
       on:change={handleChange}
       class="dp-fields-checkbox"
       title="{dp_trans('Click to select fields')}"
       type="checkbox"
       data-cy="fields-checkbox"
/>

<style lang="scss">.dp-fields-checkbox {
  vertical-align: middle;
  cursor: pointer;
  opacity: 0;
  transition: opacity ease-in-out 0.4s;
}

:global(.dp_fields_list):hover .dp-fields-checkbox {
  opacity: 1;
}</style>
