import type { AdminUiStore, AdminUiStoreData } from "@ProductConfig/types/UiStore";
import { writableStore } from "@utils/vegemite";

export function createUiStore<T> (state: AdminUiStoreData): AdminUiStore {
  const store = writableStore(state);

  const { update } = store;

  return {
    ...store,
    setTabHasError: (tab_name, id, has_error) => {
      return update((state) => {
        // @ts-ignore
        let map = state.tab_errors.get(tab_name);
        if (!map) {
          map = new Map();
          // @ts-ignore
          state.tab_errors.set(tab_name, map);
        }
        map.set(id, has_error);
        // @ts-ignore
        state.tab_errors.set(tab_name, map);
        return state;
      });
    },

    setLoading (loading: boolean) {
      return update((state) => {
        state.loading = loading;
        return state;
      });
    }
  };
}
