function getNextInput (td: JQuery, input: JQuery, next_fn: string) {
  const inputs = td.find("input");
  let index = inputs.index(input);
  if (index === -1) {
    return false;
  }
  (next_fn === "next") ? index++ : index--;
  if (index === -1) {
    return false;
  }
  const next_input = inputs.eq(index);
  if (next_input.prop("tabindex") === -1 || next_input.hasClass('dp-skip')) {
    return getNextInput(td, next_input, next_fn);
  }
  return next_input;
}

const keyUpHandler = (e) => {
  const target = e.target as HTMLInputElement;
  const tag = target.tagName;
  if (tag.toLowerCase() !== "input") {
    return;
  }
  const key = e.key;
  if (key !== "ArrowDown" && key !== "ArrowUp") {
    return;
  }
  const keydown = (key === "ArrowDown");

  const jqTarget = jQuery(target);
  const td = jqTarget.closest("td");
  if (!td.length) {
    return;
  }

  const next_fn = keydown ? "next" : "prev";
  const next_sibling = getNextInput(td, jqTarget, next_fn);
  if (next_sibling && next_sibling.length) {
    next_sibling.focus().select();
    return;
  }

  const index = td.index();

  const fn = `${ next_fn }All`;
  const tr = td.parent();
  const next_tr = tr[fn]("tr").filter((i, tr) => {
    return $(tr).find("td").eq(index).find("input").length > 0;
  }).first();

  if (!next_tr.length) {
    return;
  }

  const next_td = next_tr.find("td").eq(index);
  const next_input = next_td.find("input:eq(0)");
  next_input.focus().select();
};
document.removeEventListener("keyup", keyUpHandler);
document.addEventListener("keyup", keyUpHandler);

export {};
