import { dpa, dpa_groups, dpa_post } from "@ProductConfig/variables";
import type { GroupsStore } from "../../../types/GroupsStore";

export async function refreshGroups () {
  const res = await dpa_post.post(dpa.controllers.product_field_groups, {
    action: "get_field_groups",
    no_msg: true
  });
  dpa_groups.updateData(res as GroupsStore);
}
