import { writable } from "svelte-local-storage-store";
import { get } from "svelte/store";

const store = writable("tips", {});

export const tips = {
  ...store,

  isDismissed (id) {
    const state = get(store);
    return state[id] === false;
  },

  dismiss (id: string) {
    return store.update(state => {
      state[id] = false;
      return state;
    });
  }
};
