<script lang="ts">import { dpa, dpa_ui } from "@ProductConfig/variables";
import { dp_trans } from "@utils/trans-helper";
import { uniqid } from "@utils/utils";
let product_link = randomizeLink(dpa.links.product_link);
function randomizeLink(link) {
    return link.replace("_rand_", uniqid());
}
</script>

