import type { ICondition } from "@interfaces/condition-interface";
import type { ConditionsStore } from "@ProductConfig/types/ConditionsStore";
import { writable } from "svelte/store";

export function createConditionsStore<ConditionStoreData> (conditions): ConditionsStore {
  const { set, update, subscribe } = writable(conditions);

  return {
    subscribe,
    update,
    set,

    updateConditions: (conditions: ICondition[]) => {
      set(conditions);
    },

    addCondition: (condition: ICondition) => {
      return update((conditions) => {
        conditions[condition.id] = condition;
        return conditions;
      });
    },

    updateCondition: (condition: ICondition) => {
      return update((conditions) => {
        conditions[condition.id] = condition;
        return conditions;
      });
    },

    deleteCondition: (condition: ICondition) => {
      return update((conditions) => {
        delete conditions[condition.id];
        return conditions;
      });
    },
  };
}
