import "./table-sort.js";

export function tablednd (node: HTMLTableElement, data?) {
  const table = jQuery(node);
  const handleClass = "dp_drag";
  (table as any).dpTableDnD({
    dragHandle : handleClass,
    onDragClass: "dp_dragged",
    onDrop     : (table: HTMLTableElement) => {
      const order = Array.from(table.tBodies[0].rows).map((row) => {
        const id = jQuery(row).data("id") || 0;
        return + id;
      }).filter(Boolean);
      const reorderEvent = new CustomEvent("reorder", { detail: order });
      node.dispatchEvent(reorderEvent);
    }
  });

  return {
    update () {
      (table as any).dpTableDnDUpdate();
    }
  };
}
