import excel from "highlight.js/lib/languages/excel"

const customFunctions = ["CONTAINS", "CHECK", "BINARY_AND", "BINARY_OR", "PRICE"]

export function modifiedExcel (hljs) {
  const def = excel(hljs)
  // Remove symbols to avoid highlighting field name like ab12
  def.contains = def.contains.filter(rule => rule.className !== "symbol")
  // @ts-ignore
  def.keywords.built_in += " " + customFunctions.join(" ")
  return def
}
