<script lang="ts">import { dpa_ui } from "@ProductConfig/variables";
import { dp_trans } from "@utils/trans-helper";
import { fade } from "svelte/transition";
export let modal_id;
export let name = "dialog";
let modal_element;
let closed = true;
$: open_modal = $dpa_ui.open_modal;
$: open_modal === modal_id && openModal();
$: open_modal === null && closeModal();
function modal(node) {
    node.addEventListener("click", (ev) => {
        if (ev.target.hasAttribute("close")) {
            closeModal();
        }
    });
}
function openModal() {
    closed = false;
}
function overlayClick(ev) {
    if (ev.target.classList.contains("dp_modal__overlay")) {
        closeModal();
    }
}
function closeModal() {
    $dpa_ui.open_modal = null;
    if (modal_element) {
        modal_element.addEventListener("animationend", () => {
            if ($dpa_ui.open_modal === null) {
                closed = true;
            }
        }, { once: true });
    }
}
function handleEscape(ev) {
    if (ev.code === "Escape") {
        closeModal();
    }
}
</script>

<svelte:body on:keyup={handleEscape}/>

<div class="dp_modal is-open" id={modal_id}
     aria-hidden="{$dpa_ui.open_modal !== modal_id}"
     bind:this={modal_element}
     use:modal
>
  <div class="dp_modal__overlay" tabindex="-1" on:mousedown={overlayClick}>
    <div class="dp_modal__container" transition:fade={{duration: 200}} data-cy={name} role="dialog" aria-modal="true"
         aria-labelledby="{modal_id}-title"
    >

      <div class="dp_modal__header">
        <slot name="header"></slot>

        <button class="dp_modal__close" close aria-label={dp_trans('Close')} data-cy="close"></button>
      </div>

      <div class="dp_modal__content" id="{modal_id}-content">
        <div class="card">
          <slot></slot>
        </div>
      </div>
    </div>
  </div>
</div>
