import type { IFieldFormula } from "@interfaces/field-formula-interface";
import type { FieldFormulasStore } from "@ProductConfig/types/FieldFormulasStore";
import { writable } from "svelte/store";

export function createFieldFormulasStore<FieldFormulasStoreData> (items): FieldFormulasStore {
  const { set, update, subscribe } = writable(items);

  return {
    subscribe,
    update,
    set,

    updateFieldFormulas: (items) => {
      set(items);
    },

    addFieldFormula: (item: IFieldFormula) => {
      return update((items) => {
        items[item.id] = item;
        return items;
      });
    },

    updateFieldFormula: (item: IFieldFormula) => {
      return update((items) => {
        items[item.id] = item;
        return items;
      });
    },

    deleteFieldFormula: (item: IFieldFormula) => {
      return update((items) => {
        delete items[item.id];
        return items;
      });
    },
  };
}
