<script lang="ts">import { dpa, dpa_message, showErrorMessage, showSuccessMessage } from "@ProductConfig/variables";
import { createEventDispatcher } from "svelte";
import Portal from "svelte-portal";
export let controller;
export let action;
export let data = {};
export let id_product = dpa.id_product;
export let valid_extensions = ["gif", "png", "jpg", "jpeg"];
export let uploader;
let dispatch = createEventDispatcher();
function upload(node) {
    jQuery(node).ajaxfileupload(Object.assign(Object.assign({ action: controller, params: Object.assign(Object.assign({ action: action, id_product }, data), { ajax: true }) }, { valid_extensions }), { onComplete: (res) => {
            if (res.success) {
                showSuccessMessage(dpa_message.success);
            }
            else {
                showErrorMessage(res.message);
            }
            node.value = "";
            dispatch("complete", res);
        } }));
}
</script>

<Portal target="body">
  <input class="dp_away" data-cy={$$props['data-cy']} type="file" name="file" use:upload bind:this={uploader}/>
</Portal>
