<script lang="ts">import { FieldTypes } from "@data/field-types";
import Modal from "@ProductConfig/components/Modal.svelte";
import { dpa_ui } from "@ProductConfig/variables";
import { dp_trans } from "@utils/trans-helper";
import CheckboxSettings from "./FieldSettings/CheckboxSettings.svelte";
import ColorPickerSettings from "./FieldSettings/ColorPickerSettings.svelte";
import DateSettings from "./FieldSettings/DateSettings.svelte";
import DropdownSettings from "./FieldSettings/DropdownSettings.svelte";
import ErrorSettings from "./FieldSettings/ErrorSettings.svelte";
import FeatureSettings from "./FieldSettings/FeatureSettings.svelte";
import FileSettings from "./FieldSettings/FileSettings.svelte";
import HtmlSettings from "./FieldSettings/HtmlSettings.svelte";
import ImageSettings from "./FieldSettings/ImageSettings.svelte";
import InputSettings from "./FieldSettings/InputSettings.svelte";
import RadioSettings from "./FieldSettings/RadioSettings.svelte";
import SwitchSettings from "./FieldSettings/SwitchSettings.svelte";
import TextSettings from "./FieldSettings/TextSettings.svelte";
import ThumbnailsSettings from "./FieldSettings/ThumbnailsSettings.svelte";
export let field;
export let modal_id;
const components = new Map([
    [FieldTypes._DP_INPUT_, InputSettings],
    [FieldTypes._DP_SLIDER_, InputSettings],
    [FieldTypes._DP_DROPDOWN_, DropdownSettings],
    [FieldTypes._DP_RADIO_, RadioSettings],
    [FieldTypes._DP_THUMBNAILS_, ThumbnailsSettings],
    [FieldTypes._DP_CHECKBOX_, CheckboxSettings],
    [FieldTypes._DP_SWITCH_, SwitchSettings],
    [FieldTypes._DP_TEXT_, TextSettings],
    [FieldTypes._DP_TEXTAREA_, TextSettings],
    [FieldTypes._DP_DATE_, DateSettings],
    [FieldTypes._DP_IMAGE_, ImageSettings],
    [FieldTypes._DP_FILE_, FileSettings],
    [FieldTypes._DP_FEATURE_, FeatureSettings],
    [FieldTypes._DP_COLORPICKER_, ColorPickerSettings],
    [FieldTypes._DP_HTML_, HtmlSettings],
    [FieldTypes._DP_ERROR_, ErrorSettings],
]);
$: component = components.get(field.type);
</script>

{#if $dpa_ui.open_modal === modal_id}
  <Modal {modal_id}>
    <span slot="header"> <i class="material-icons">settings</i> {dp_trans('Field settings')} </span>
    {#if component}
      <svelte:component this={component} {field} {modal_id}/>
    {/if}
  </Modal>
{/if}
