<script lang="ts">import { createEventDispatcher } from "svelte";
const dispatch = createEventDispatcher();
export let name = null;
export let value;
export let label;
export let desc = null;
export let cls = null;
export let minimal = false;
function toggleSwitch() {
    value = +!value;
    dispatch("change", { name, value: +value });
}
</script>

<div class="form-group {cls || ''}" class:minimal data-cy={name} data-cy-checked={!!value}>
  <label on:click={toggleSwitch}>
    <div class="switch-input" class:-checked={!!value}></div>
    {label}
    {#if desc}
      <div class="help-block">{desc}</div>
    {/if}
  </label>
</div>

<style lang="scss">.form-group.minimal {
  padding: 3px;
  margin-left: 0;
  margin-bottom: 0;
}
.form-group.minimal label {
  margin: 0;
}
.form-group.minimal .help-block {
  margin: 5px;
}</style>
