import { dpa, dpa_fields, dpa_post } from "@ProductConfig/variables";
import { get } from "svelte/store";

let getNewPositions = function () {
  let newPositions = {};
  jQuery("#dp_field_groups_container .dp-fields-sortable .dp_equation_btn").each((index, element) => {
    newPositions[index + 1] = $(element).data("id");
  });
  return newPositions;
};

async function saveNewPositions (newPositions) {
  await dpa_post.post(dpa.controllers.product_fields, {
    action: "save_fields_order",
    order : newPositions,
  });
}

function sortable (node, { id_group, dpa_groups }) {
  const jqNode = jQuery(node);
  jqNode.sortable({
    placeholder: "dp_field_highlight dp_equation_btn",
    revert     : 200,
    opacity    : .8,
    connectWith: ".dp-fields-sortable",
    tolerance  : "pointer",
    start      : (event, ui) => {
      ui.placeholder.html(ui.helper.html());
    },
    stop       : (event, ui) => {
      const id_field = ui.item.data("id");
      if (jqNode.find(`.dp_equation_btn[data-id=${ id_field }]`).length) {
        const newPositions = getNewPositions();
        saveNewPositions(newPositions);
        updatePositions(newPositions);
      }
    },
    receive    : async (event, ui) => {
      const id_field = ui.item.data("id");
      const current_group = dpa_fields[id_field] && dpa_fields[id_field].id_group;
      if (current_group !== id_group) {
        const newPositions = getNewPositions();
        await Promise.all([
          dpa_post.post(dpa.controllers.product_field_groups, {
            action: "save_field_group",
            no_msg: true,
            id_group,
            id_field,
          }),
          saveNewPositions(newPositions)
        ]);
        updatePositions(newPositions);
        updateFieldGroup(id_field, id_group);
      }
    },
  }).disableSelection();
}

function updatePositions (newPositions) {
  const fields = get(dpa_fields);
  for (const position in newPositions) {
    if (newPositions.hasOwnProperty(position)) {
      const id_field = newPositions[position];
      if (fields[id_field]) {
        fields[id_field].position = +position;
      }
    }
  }
  dpa_fields.set(fields);
}

function updateFieldGroup (id_field, id_group) {
  const fields = get(dpa_fields);
  if (fields[id_field]) {
    fields[id_field].id_group = id_group;
  }
  dpa_fields.set(fields);
}

export { sortable };
