import { IResponseData } from "@interfaces/response-interface";
import { StepsStoreData } from "@ProductConfig/types/StepsStore";
import { dpa, dpa_post, dpa_steps } from "@ProductConfig/variables";

export async function refreshSteps () {
  const res: IResponseData<StepsStoreData> = await dpa_post.post(dpa.controllers.product_steps, {
    action: "get_steps",
    no_msg: true
  });
  dpa_steps.updateData(res);
}
