import type { IField } from "@interfaces/field-interface";
import { writable } from "svelte/store";
import type { AdminFieldStore } from "../types/FieldsStore";

export function createFieldsStore<AdminFieldStoreData> (fields): AdminFieldStore {
  const { set, update, subscribe } = writable(fields);

  return {
    subscribe,
    update,
    set,

    updateFields: (fields: IField[]) => {
      set(fields);
    },

    addField: (field: IField) => {
      return update((fields) => {
        fields[field.id] = field;
        return fields;
      });
    },

    updateField: (field: Partial<IField>) => {
      return update((fields) => {
        fields[field.id] = { ...fields[field.id], ...field };
        return fields;
      });
    },

    deleteField: (field: IField) => {
      return update((fields) => {
        delete fields[field.id];
        return fields;
      });
    },
  };
}
