import type { Readable, Writable } from "svelte/store";
import { derived, get, writable } from "svelte/store";

export function undoable<T> (store: Writable<T>): [Writable<T>, () => void, () => void, Readable<boolean>, Readable<boolean>] {
  const initial = get(store);

  const state = writable({
    value: initial,
    stack: [initial],
    index: 0,
  });

  const undo = () => {
    state.update(({ stack, index, value }) => {
      if (index > 0) {
        store.set(value = stack[--index]);
      }
      return { value, stack, index };
    });
  };

  const redo = () => {
    state.update(({ stack, index, value }) => {
      if (index < stack.length - 1) {
        store.set(value = stack[++index]);
      }
      return { value, stack, index };
    });
  };

  const update = fn => {
    store.update(old_value => {
      const value = fn(old_value);
      if (value !== old_value) {
        state.update(({ stack, index }) => {
          stack.length = ++index; // clear forward history
          stack[index] = value;
          return { value, stack, index };
        });
      }
      return value;
    });
  };

  const set = value => {
    update(() => value);
  };

  const value = derived(state, ({ value }) => value);
  const new_store = { subscribe: value.subscribe, update, set };

  const can_undo = derived(state, ({ index }) => index > 0);
  const can_redo = derived(state, ({ index, stack }) => index < stack.length - 1);

  return [new_store, undo, redo, can_undo, can_redo];
}
