import type { IProportion } from "@interfaces/proportion-interface";
import type { ProportionsStore } from "@ProductConfig/types/ProportionsStore";
import { writable } from "svelte/store";

export function createProportionsStore<ProportionsStoreData> (items): ProportionsStore {
  const { set, update, subscribe } = writable(items);

  return {
    subscribe,
    update,
    set,

    updateProportions: (items: IProportion[]) => {
      set(items);
    },

    addProportion: (item: IProportion) => {
      return update((items) => {
        items[item.id] = item;
        return items;
      });
    },

    updateProportion: (item: IProportion) => {
      return update((items) => {
        items[item.id] = item;
        return items;
      });
    },

    deleteProportion: (item: IProportion) => {
      return update((items) => {
        delete items[item.id];
        return items;
      });
    },
  };
}
