import { dpa_ui } from "@ProductConfig/variables";
import { get } from "svelte/store";

export function clearIntervalErrors (pattern: string, add_suffix = true) {
  const tab_errors = get(dpa_ui).tab_errors;
  let regExp = new RegExp("^" + pattern.toString().replace("*", ".*?") + (add_suffix ? "-" : ""));
  console.log(regExp);
  Array.from(tab_errors.get("intervals").keys())
    .filter(key => regExp.test(key))
    .forEach(key => {
      dpa_ui.setTabHasError("intervals", key, false);
    });
}
