<script lang="ts">import { getTabHasError } from "@ProductConfig/utils/ui-helper";
import { dpa, dpa_ui } from "@ProductConfig/variables";
import { dp_trans } from "@utils/trans-helper";
import { values } from "@utils/utils";
import { onMount } from "svelte";
const anchor_prefix = "dp_anchor_";
const tab_prefix = "dp_tab_";
function updateHash(event) {
    let href = event.target.getAttribute("href");
    if (href) {
        location.hash = href.replace(tab_prefix, anchor_prefix);
    }
}
let anchors;
onMount(() => {
    const hash = location.hash;
    if (hash) {
        const anchor = jQuery(anchors).find(`[href="${hash.replace(anchor_prefix, tab_prefix)}"]`);
        if (anchor.length) {
            anchor.click();
        }
    }
});
</script>

<ul on:click={updateHash} bind:this={anchors}>
  <li>
    <a href="#dp_tab_product_settings" data-cy="settings-tab">
      <i class="material-icons">settings</i> {dp_trans('Settings')}
    </a>
  </li>
  <li>
    <a href="#dp_tab_fields" data-cy="fields-tab">
      <i class="material-icons">list</i> {dp_trans('Fields')}
    </a>
  </li>
  <li>
    <a href="#dp_tab_formulas" data-cy="formulas-tab" class:dp_danger={getTabHasError('formulas', $dpa_ui.tab_errors)}>
      <i class="material-icons">functions</i> {dp_trans('Formulas')}
    </a>
  </li>
  {#if dpa.combinations && values(dpa.combinations).length}
    <li id="dp_combinations_tab_btn" data-cy="combinations_tab">
      <a href="#dp_tab_combinations">
        <i class="material-icons">settings</i> {dp_trans('Combinations')}
      </a>
    </li>
    <li id="dp_visibility_tab_btn" data-cy="visibility_tab">
      <a href="#dp_tab_visibility">
        <i class="material-icons">visibility</i> {dp_trans('Visibility')}
      </a>
    </li>
  {/if}
  <li>
    <a href="#dp_tab_conditions"
       data-cy="conditions-tab"
       class:dp_danger={getTabHasError('conditions', $dpa_ui.tab_errors)}
    >
      <i class="material-icons">call_split</i> {dp_trans('Conditions')}
    </a>
  </li>
  <li>
    <a href="#dp_tab_field_formulas"
       data-cy="field-formulas-tab"
       class:dp_danger={getTabHasError('field_formulas', $dpa_ui.tab_errors)}
    >
      <i class="material-icons">functions</i> {dp_trans('Field Formulas')}
    </a>
  </li>
  <li>
    <a href="#dp_tab_proportions" data-cy="proportions-tab">
      <i class="material-icons">repeat</i> {dp_trans('Proportions')}
    </a>
  </li>
  <li>
    <a href="#dp_tab_intervals"
       data-cy="intervals-tab"
       class:dp_danger={getTabHasError('intervals', $dpa_ui.tab_errors)}
    >
      <i class="material-icons">timeline</i> {dp_trans('Intervals')}
    </a>
  </li>
  <li>
    <a href="#dp_tab_grids" data-cy="grids-tab">
      <i class="material-icons">view_comfy</i> {dp_trans('Grids')}
    </a>
  </li>
  <li>
    <a href="#dp_tab_field_groups" data-cy="field-groups-tab">
      <i class="material-icons">group_work</i> {dp_trans('Groups')}
    </a>
  </li>
  <li>
    <a href="#dp_tab_steps" data-cy="steps-tab">
      <i class="material-icons">format_list_numbered</i> {dp_trans('Steps')}
    </a>
  </li>
</ul>
