    <i class="material-icons">info</i>
    {@html kbd(dp_trans('Use _Up__ / _Down__ arrows and _Tab__ / _Shift__ _Tab__ to navigate between fields'))}
    <button on:click={() => tips.dismiss(id)} class="dismiss btn btn-small btn-secondary">
      <i class="material-icons">close</i>
    </button>
  </p>
{/if}

<style lang="scss">.dismiss {
  opacity: 0;
  transition: opacity ease-in-out 0.4s;
}

.help-block:hover .dismiss {
  opacity: 1;
}</style>
