<script lang="ts">import Modal from "@ProductConfig/components/Modal.svelte";
import { dpa_ui } from "@ProductConfig/variables";
import { dp_trans } from "@utils/trans-helper";
import { values } from "@utils/utils";
import ConditionOption from "./ConditionOption.svelte";
export let field;
export let condition;
export let toggleField;
$: visible = !condition.hidden_fields.includes(field.id);
let modal_id = `options-visibility-${condition.id}-${field.id}`;
let has_hidden_options;
$: has_hidden_options = condition.hidden_options.length > 0;
</script>

<button on:click|preventDefault={(ev) => toggleField(ev, field.id)}
        title="{dp_trans('Click to toggle the field visibility')}"
        class="dp_equation_btn dp_field_btn"
        class:dp_condition_hidden={!visible}
>
  {field.name}
  {#if values(field.options).length}
    <button on:click={() => $dpa_ui.open_modal = modal_id}
            class="dp_btn dp_options_btn"
            class:has_hidden_options={has_hidden_options}
            title="{dp_trans('Configure options visibility')}"
    >
      <i class="material-icons">settings</i>
    </button>
    {#if $dpa_ui.open_modal === modal_id}
      <Modal {modal_id}>
      <span slot="header">
        <i class="material-icons">settings</i> {dp_trans('Options visibility')}
      </span>
        {#each values(field.options) as option}
          <ConditionOption {condition} {option}/>
        {/each}
      </Modal>
    {/if}
  {/if}
</button>

<style>
  .dp_equation_btn {
    background-color: #8bc34a;
  }

  .dp_options_btn {
    color: #f55623;
    padding: 0;
  }

  .has_hidden_options {
    color: #9E9E9E;
    text-shadow: 0 0 1px #000;
  }

</style>
