import type { VegemiteStore } from "@interfaces/SvelteStore"
import { writableStore } from "@utils/vegemite"


type FieldsSelectionState = {
  selecting: boolean;
  selected: Record<number, boolean>;
}

type FieldsSelectionStore = VegemiteStore<FieldsSelectionState> & {
  setSelecting (selecting: boolean);
  setSelected (id_field: number, checked: boolean);
  clearSelection ();
}

export enum FieldsSelectionTopics {
  SELECT_ALL,
  DESELECT_ALL
}

const store: VegemiteStore<FieldsSelectionState> = writableStore({
  selecting: false,
  selected : {}
})

export const fieldsSelection: FieldsSelectionStore = {
  ...store,

  setSelecting (selecting) {
    return store.update((state) => {
      state.selecting = selecting
      return state
    })
  },

  setSelected (id_field, checked) {
    return store.update((state) => {
      if (checked) {
        state.selected[id_field] = true
      } else {
        delete state.selected[id_field]
      }
      return state
    })
  },

  clearSelection () {
    return store.update((state) => {
      state.selected = {}
      return state
    })
  }
}
