import { trigger } from "@utils/utils";

function select2 (node: HTMLElement, params?) {
  function formatState (state) {
    const option = $(state.element);
    const color = option.data("color");

    if (!color) {
      return state.text;
    }

    return $(`<span style="color: ${ color }">${ state.text }</span>`);
  };

  function transformSelect () {
    jQuery(node).select2({
      width            : params?.width || 250,
      dropdownParent   : params?.dropdownParent || jQuery("#module_dynamicproduct"),
      templateResult   : formatState,
      templateSelection: formatState,
    }).on("change.select2", (e) => {
      trigger(node, "select", (e.target as HTMLSelectElement).value);
    });
  };

  if (document.readyState !== "complete") {
    window.addEventListener("load", () => {
      transformSelect();
    });
  } else {
    transformSelect();
  }
}

export { select2 };
