<script lang="ts">import { onDestroy, onMount } from "svelte";
let loaded = false;
let root;
let observer = new IntersectionObserver((entries) => {
    entries.forEach((entry) => {
        if (entry.isIntersecting) {
            loaded = true;
            observer.disconnect();
        }
    });
});
onMount(() => {
    observer.observe(root);
});
onDestroy(() => {
    observer.disconnect();
});
</script>

<div bind:this={root}>
  {#if loaded}
    <div>
      <slot></slot>
    </div>
  {/if}
</div>
