import type { IInterval, IIntervalCondition, IIntervalConditionGroup, IIntervalField } from "@interfaces/interval-interface";
import type { IntervalsStoreData } from "@ProductConfig/types/IntervalsStore";
import { writable } from "svelte/store";

export function createIntervalsStore<IntervalStore> (intervals: IntervalsStoreData) {
  const { set, update, subscribe } = writable(intervals);

  return {
    subscribe,
    update,
    set,

    updateIntervals: (intervals: IInterval[]) => {
      set(intervals);
    },

    addInterval: (interval: IInterval) => {
      return update((intervals) => {
        intervals[interval.id] = interval;
        return intervals;
      });
    },

    updateInterval: (interval: IInterval) => {
      return update((intervals) => {
        intervals[interval.id] = interval;
        return intervals;
      });
    },

    deleteInterval: (interval: IInterval) => {
      return update((intervals) => {
        delete intervals[interval.id];
        return intervals;
      });
    },

    updateConditionGroup: (condition_group: IIntervalConditionGroup) => {
      return update((intervals) => {
        intervals[condition_group.id_interval]
          .intervalConditionGroups[condition_group.id] = condition_group;
        return intervals;
      });
    },

    deleteConditionGroup: (condition_group: IIntervalConditionGroup) => {
      return update((intervals) => {
        delete intervals[condition_group.id_interval].intervalConditionGroups[condition_group.id];
        return intervals;
      });
    },

    addIntervalField: (interval_field: IIntervalField) => {
      return update((intervals) => {
        intervals[interval_field.id_interval].intervalFields[interval_field.id] = interval_field;
        return intervals;
      });
    },

    deleteIntervalField: (interval_field: IIntervalField) => {
      return update((intervals) => {
        delete intervals[interval_field.id_interval].intervalFields[interval_field.id];
        return intervals;
      });
    },

    updateIntervalCondition: (id_interval: number, interval_condition: IIntervalCondition) => {
      return update((intervals) => {
        intervals[id_interval]
          .intervalConditionGroups[interval_condition.id_interval_condition_group]
          .intervalConditions[interval_condition.id] = interval_condition;
        return intervals;
      });
    },

    deleteIntervalCondition: (id_interval: number, interval_condition: IIntervalCondition) => {
      return update((intervals) => {
        delete intervals[id_interval]
          .intervalConditionGroups[interval_condition.id_interval_condition_group]
          .intervalConditions[interval_condition.id];
        return intervals;
      });
    },
  };
}
