import type { IField } from "@interfaces/field-interface";
import type { Partial } from "@interfaces/utils";
import { modifiedExcel } from "@ProductConfig/utils/excel";
import { dpa, dpa_fields, dpa_message } from "@ProductConfig/variables";
import { values } from "@utils/utils";
import { FieldConfig } from "@views/ts/product-config/tabs/Fields/Field/field-config";
import * as formatter from "excel-formula";
import hljs from "highlight.js/lib/core";
import { get } from "svelte/store";

hljs.registerLanguage("excel", modifiedExcel);

export function getFieldExpression (field: Partial<IField>) {
  const name = field.name;
  const fieldConfig = FieldConfig.get(+field.type || 1);
  if (fieldConfig.text) {
    return `"[${ name }]"`;
  }
  return `[${ name }]`;
};

export function getSecondaryFieldExpression (field: Partial<IField>) {
  let fieldExpression = getFieldExpression(field);
  fieldExpression = fieldExpression.replace("[", "[[");
  fieldExpression = fieldExpression.replace("]", "]]");
  return fieldExpression;
};

let reformatFormula = function (formula: string) {
  formula = formula.replace(/\s*$/, "");
  formula = formatter.formatFormula(formula, {
    autoLineBreak: "",
    newLine      : " ",
  });
  formula = formula.replace(/! =/g, "!=");
  formula = formula.replace(/\n/g, " ").replace(/\s+/g, " ");
  return formula;
};

export function getFormattedFormula (formula: string, reformat = true): string {
  if (!formula) {
    return "";
  }

  if (reformat) {
    try {
      formula = reformatFormula(formula);
      formula = hljs.highlight("excel", formula).value;
    } catch {
    }
  }

  const fields = getAvailableFieldNames();
  return formula.replace(/(\[+)(.*?)(]+)/g, (matched, opening_brackets, field_name, closing_brackets) => {
    let invalid = "";
    if (fields && !fieldExists(fields, field_name)) {
      invalid = "dp_unavailable";
    }
    return `<b${ invalid ? ` title="${ dpa_message.fields.unavailable }" class="${ invalid }"` : "" }>${ opening_brackets }${ field_name }${ closing_brackets }</b>`;
  });
}

function fieldExists (fields: string[], field_name: string): boolean {
  return fields.includes(field_name);
}

export function getAvailableFieldNames () {
  const fields = [];

  const fields_list: { [id: number]: IField; } = get(dpa_fields);
  fields.push(...values(fields_list).map(field => field.name));

  fields.push(...dpa.attributes.map(attribute => attribute.name));
  fields.push(...dpa.features.map(feature => feature.name));

  fields.push("product_price", "product_weight", "changed", "quantity");

  return fields;
}

export function insertHtml (html) {
  var sel, range;
  if (window.getSelection) {
    // IE9 and non-IE
    sel = window.getSelection();
    if (sel.getRangeAt && sel.rangeCount) {
      range = sel.getRangeAt(0);
      range.deleteContents();

      // Range.createContextualFragment() would be useful here but is
      // only relatively recently standardized and is not supported in
      // some browsers (IE9, for one)
      var el = document.createElement("div");
      el.innerHTML = html;
      var frag = document.createDocumentFragment(), node, lastNode;
      while ((node = el.firstChild)) {
        lastNode = frag.appendChild(node);
      }
      range.insertNode(frag);

      // Preserve the selection
      if (lastNode) {
        range = range.cloneRange();
        range.setStartAfter(lastNode);
        range.collapse(true);
        sel.removeAllRanges();
        sel.addRange(range);
      }
    }
  }
}

export function cleanFormula (formula) {
  return formula.trim().replace(/\u00A0/g, " ");
}
